<template>
  <div>
    <div class="container" v-show="!visibleFlag">
      <div class="top">
        <div class="text">
          <span class="letter" v-for="(char, index) in message" :key="index">{{ char }}</span>
        </div>
      </div>
      <div class="bottom">
        <button v-if="visibleChars === message.length" class="button" @click="showPages">Enter</button>
      </div>
    </div>
    <div class="pages" v-show="visibleFlag">
      <router-view v-show="visibleFlag"/>
    </div>
  </div>
</template>

<script>
import NavMenu from './components/common/NavMenu.vue'
export default {
  name: 'App',
  components: {NavMenu},
  data () {
    return {
      message: '自动驾驶安全测试平台',
      visibleFlag: false,
      visibleChars: 0
    }
  },
  mounted () {
    // 从localStorage中读取visibleFlag的值
    const flag = localStorage.getItem('visibleFlag')
    if (flag === 'true') {
      this.visibleFlag = true
    } else {
      this.showText()
    }
  },
  methods: {
    showText () {
      let letters = document.querySelectorAll('.letter')
      letters.forEach((letter, index) => {
        setTimeout(() => {
          letter.style.opacity = '1'
          letter.style.transform = 'translateX(0)'
          this.visibleChars += 1
        }, 50 * index)
      })
    },
    showPages () {
      // 将visibleFlag存储到localStorage中
      localStorage.setItem('visibleFlag', 'true')
      this.visibleFlag = true
    }
  }
}
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /*background-color: #e6e6e6;*/
  /*background-color: #4876FF;*/
}

.text {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.letter {
  opacity: 0;
  transform: translateX(-20px);
  font-size: 80px;
  font-weight: bold;
  color: #333;
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

.button {
  opacity: 0;
  border: none;
  font-size: 20px;
  background-color: transparent;
  animation-name: floatUp;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  /*background-color: #5daf34;*/
}

@keyframes floatUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>



